// Main
.main {
	text-align: center;
	.content {
		@include clr;
		padding: 0 15px;
		width: auto;
		margin: 0 215px; } }


// Sidebar
.sidebar {
	width: 100%;
	max-width: 215px;
	display: inline-block;
	vertical-align: top;
	&.sidebar__left {
		float: left;
		.post {
			border-top: 1px solid $separator;
			border-bottom: 1px solid $separator; }
		.banner {
			margin-bottom: 20px; } }
	&.sidebar__right {
		float: right;
		.post {
			border-bottom: 1px solid $separator; }
		.banner__img {
			border: 1px solid $separator; } } }

.features {
	padding-top: 10px;
	margin-bottom: 20px;
	text-align: left;
	&__btn {
		float: right; }
	&__title {
		color: $blue-darker;
		display: inline-block;
		vertical-align: top;
		@include text-overflow;
		max-width: 65%; } }
// banner
.banner {
	text-decoration: none;
	text-align: left;
	line-height: 1;
	display: block;
	margin: 0 auto;
	max-width: 210px;
	width: 100%;
	font-size: 0;
	&__img {
		display: block;
		@include cover;
		height: 300px;
		width: 100%; }
	&__title {
		font-size: $text-sm; //14px
		margin: 20px auto 17px;
		color: $blue-gray;
		display: inline-block;
		vertical-align: top; }
	&__caption {
		font-size: $text-exs; //11px
		color: $gray-lighter;
		margin-top: 8px;
		line-height: 1;
		display: block;
		a {
			color: inherit;
			text-decoration: none;
			transition: color .24s ease-in-out; }
		a:hover {
			text-decoration: underline;
			color: $green-darker; } } }
