input.error,
input.valid,
select.error,
select.valid {
	background-position: right 24px center; }
input.error {
	border-color: red !important; }

.textarea {
	width: 100%;
	resize: none;
	font-size: $text-reg; //16px
	border: 1px solid $white;
	@include placeholder-color($white);
	box-shadow: inset 0 0 30px 5px rgba(62, 53, 42, .11);
	padding: 24px 24px 24px 38px;
	letter-spacing: 0.200rem;
	text-transform: uppercase;
	transition: border-color .32s ease, box-shadow .42s ease; }

.input {
	&-field {
		display: inline-block;
		vertical-align: top;
		width: 100%; } }

// Input Style
.input {
	display: inline-block;
	vertical-align: top;
	margin: 0;
	font-size: $text-reg; //16px
	width: 100%;
	padding: 0;
	@include placeholder-color($white); }
